.goodsList {
  width: auto;
  height: auto;
  display: flex;
  flex-wrap: wrap; }
  .goodsList .goodsItem {
    width: 50%;
    background: white;
    height: auto;
    padding: .2rem;
    position: relative; }
    .goodsList .goodsItem .goodsItemImg {
      width: 100%;
      min-height: 2rem;
      height: auto; }
      .goodsList .goodsItem .goodsItemImg img {
        width: 100%;
        height: auto; }
    .goodsList .goodsItem .goodsItemName {
      text-align: center;
      font-size: .28rem;
      overflow: hidden;
      text-overflow: ellipsis;
      white-space: nowrap;
      padding-bottom: .3rem;
      padding-top: .1rem; }
    .goodsList .goodsItem .goodsItemPrice {
      text-align: center;
      font-size: .28rem;
      overflow: hidden;
      text-overflow: ellipsis;
      white-space: nowrap;
      color: red; }
  .goodsList .goodsItem:nth-child(2n+1):after {
    content: '';
    position: absolute;
    top: 0;
    left: 0;
    border: 1px solid #f5f5f9;
    border-left-width: 2px;
    box-sizing: border-box;
    width: 200%;
    height: 200%;
    transform: scale(0.5);
    transform-origin: left top; }
  .goodsList .goodsItem:nth-child(2n):after {
    content: '';
    position: absolute;
    top: 0;
    left: 0;
    border: 1px solid #f5f5f9;
    border-right-width: 2px;
    box-sizing: border-box;
    width: 200%;
    height: 200%;
    transform: scale(0.5);
    transform-origin: left top; }

.imgLazyload.loading {
  opacity: 0.5; }

.imgLazyload.loadEnd {
  opacity: 1;
  transition: all 1s; }

.tabWrap {
  position: fixed;
  top: 1rem;
  left: 0;
  width: 100%;
  height: auto;
  z-index: 10; }
  .tabWrap .am-tabs-vertical.am-tabs-tab-bar-wrap {
    width: 2rem; }
  .tabWrap .am-tabs-default-bar-tab {
    height: .64rem; }
  .tabWrap .tabsItem {
    font-size: .28rem; }

.categoryDetail {
  padding: .2rem 0;
  margin-top: .64rem; }

.categoryDetail > div {
  height: .6rem;
  line-height: .6rem;
  text-align: center; }

.categoryDetail > div:nth-child(2) {
  color: gray;
  height: .5rem;
  line-height: .5rem;
  font-size: .28rem; }

.header{
    position: fixed;
    top: 0;
    left: 0;
    width: 100%;
    height: 1rem;
    background: white;
    box-shadow: 0 .04rem .08rem 0 rgba(0,0,0,.12), 0 0 6px 0 rgba(0,0,0,.04);
    display: flex;
    justify-content: center;
    align-items: center;
    z-index: 10;
}
.header>div{
    height: 100%;
    display: flex;
    justify-content: center;
    align-items: center;
}
.header .left,.header .right{
    width: .6rem;
}
.header .title{
    flex: 1 1;
    font-size: .32rem;
}
